@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Inter', sans-serif;
  /* background-color: #2c2c2c; */
  text-decoration: none;
}

:visited{
  color: inherit;
}
.app__page {
  display: -webkit-flex;
  display: flex;
}

.sidebar {
  width: 15vw;
  -webkit-flex: 0.15 1;
          flex: 0.15 1;
  height: 100vh;
  border-right: 1px solid lightgray;
}

.sidebar__account {
  margin-top: 20px;
  margin-left: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.sidebar__accountName > h4 {
  margin-left: 10px;
  margin-right: 10px;
  color: gray;
}
.sidebar__accountName > h5 {
  margin-left: 10px;
  color: #808191;
}

.sidebar__logo {
  display: block;
  margin: 20px 20px;
  height: 75px;
}

.sidebarRow {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 13px 20px;
  border-top-right-radius: 25px;
  border-bottom-right-radius: 25px;
  width: 80%;
  transition: 0.2s ease;
}
.sidebarRow:hover {
  background-color: #508ef1;
  background-color: #045de9;
  font-size: large !important;
  cursor: pointer;
}
.sidebarRow:hover > .sidebarRow__icon {
  color: white;
}
.sidebarRow:hover > .sidebarRow__title {
  color: white;
}

.sidebarRow__icon {
  color: #a3a3a5;
  font-size: 24px !important;
}

.sidebarRow.selected > .sidebarRow__icon {
  color: white;
}
.sidebarRow__title {
  -webkit-flex: 1 1;
          flex: 1 1;
  color: gray;
  margin-left: 20px;
  font-size: 14px;
  font-weight: 500;
}
.sidebarRow.selected {
  background-color: #045de9;
}
.sidebarRow.notImplemented:hover {
  cursor: not-allowed;
}
/* .sidebarRow.selected > .sidebarRow__icon {
  color: red;
} */

.sidebarRow.selected > .sidebarRow__title {
  font-weight: bold;
  color: white;
}

.homepage {
  -webkit-flex: 0.85 1;
          flex: 0.85 1;
  background-color: #f9f9f9;
  padding: 40px 20px;
  padding-bottom: 0;
}

.attendanceCalendar{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.eventRow {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.eventRow__events {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.eventRow__arrow {
  display: -webkit-flex;
  display: flex;
  margin-right: 10px;
  box-sizing: content-box;
  padding: 10px;
  border-radius: 1000px;
  border: 1px solid lightgray;
  /* box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.08); */
}
.eventRow__arrow:hover {
  background-color: lightgray;
}

.event{
    background: linear-gradient(to left top, #045de9, #508ef1);

    background-color: #508ef1;
    background-color: #045de9;
    border-radius: 30px;
    padding: 20px;
    padding-bottom: 10px;
    margin: 20px;
    width: 300px;
    color: white;
    box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.08);
}

.event__title{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    font-size: 18px;
    margin-bottom: 10px;
}

.event__type{
    font-size: 12px;
    font-weight: 400;
    letter-spacing: 1px;
    padding: 6px;
    border-radius: 10px;
    background: linear-gradient(to right,rgb(19, 171, 231), lightblue);
    box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.08);
    color: rgb(56, 55, 55);
}

.event__description > h4{
    font-weight: 200;
    margin-bottom: 10px;
}

.event__date{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}
*:focus {
  outline: none;
}

